.SecondScreen {
  position: fixed;
  width: 100vw;
  height: 100%;

  animation: fadein;
  animation-duration: 5s;
}

@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.SecondScreen_foodSection {
  height: 60%;
  padding: 5%;
}
.SecondScreen_foodPicture {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.SecondScreen_picture {
  max-width: 100%;
  max-height: 100%;
}

.SecondScreen_descriptionSection {
  height: 40%;
  padding-left: 5%;
  padding-right: 5%;
  padding-bottom: 5%;
}

.SecondScreen_description {
  height: 70%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  border: 3px solid black;
  border-radius: 1.5rem;
}

.SecondScreen_text1 {
  position: relative;
  display: inline-block;
  height: 5vmax;
  overflow: hidden;
  animation: typingAni1 4s steps(22, end) 1;
}
@keyframes typingAni1 {
  0% {
    width: 0%;
  }
  100% {
    width: 70vw;
  }
}

.SecondScreen_foodName {
  color: #dc3545;
}

.SecondScreen_buttonSection {
  height: 30%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  animation: fadeoff;
  animation-duration: 10s;
}
.SecondScreen_button_appeal {
  height: 7vh;
  border-radius: 1rem;
  font-size: 3vmax;
}

@keyframes fadeoff {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
